body {
    background-color:rgb(245, 228, 209)!important;
}




/*navbar start*/

.navbar {
    background-color: rgb(0,159,128);
    padding-bottom: 23px;
    
}

.navbar ul li  {
    display: inline-block;
    list-style: none;
    margin-left: 10px;
    margin-right: 10px;
  
    
    
    
}

.navbar .nav_items {
    margin-top: 10px;
}





 .navbar .active {
    color: #fff;
}

.navbar a {
    text-decoration: none;
    color: rgba(63, 58, 58, 0.767);
    font-weight: bold;
  margin-left: 20px;

}
.navbar .nav_items a:hover {
    color: #fff;
}

.navbar .totalItems {
    color: #fff;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    background-color: red;
    border-radius: 50%;
    width: 30px;
    margin-left: 5px;
    margin-right: 10px;
    
   
    
    
}
.navbar a:hover {
    color: rgb(0, 0, 0);
    text-decoration: none;
}


/*navbar end*/





/*shop start*/



.welcome {
    background: url('./public/wlcome.jpg')  bottom;
    width: 100%;
    height: 300px;
    margin-top: 35px;
}

.welcome .overlay {
    background: rgba(0, 0, 0, 0.4);
    width: 100%;
    height: 100%;
}

.welcome .overlay h1 {
    font-family: serif;
    font-weight: bold;
    text-align: center;
    font-size: 50px;
    padding-top: 100px;
    color: #fff;
 
 }
 .welcome .overlay h1 span {
     color: rgb(0,159,128);
 }



.mainProducts {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 70%;
    height: 100%;
    margin: 50px auto;
    padding: 50px 0 0;
    justify-content: space-between;
    flex-wrap: wrap;
    font-family: serif;

}

.products {
    width: 200px;
    margin-bottom: 20px;
    border: 0px;
    background-color: rgb(255, 255, 255);
    border-radius: 35px;
    
}

 .mainProducts .productCard img {
    width: 100%;
    height: 200px;
    

}

.mainProducts .productCard .productInfo {
      text-align: center;
        margin-top: 10px;
}

.mainProducts .productCard .productInfo p {
    font-weight: bold;
    color: rgb(0, 124, 99);
}



 .btn-cart {
    background-color: rgb(0,159,128);
    color: rgb(255, 255, 255);
    width: 100px;
    border: 0px;
    border-radius: 15px;
   margin-bottom: 20px;
    
 
}
.loading {
    margin-top: 70px;
}

/*shop end*/

/*cart start*/
.cart {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 70%;
    height: 100%;
    margin: 50px auto;
    justify-content: space-between;
    flex-wrap: wrap;
    font-family: serif;
    
}

.cartItem {
    width: 200px;
    height: 100%;
    margin-bottom: 20px;
    border: 0px solid rgb(0,159,128);
    background-color: #fff;
    border-radius:  25px;
    text-align: center;
}

.theCart h1 {
    margin-top: 60px;
    font-family: serif;
    text-align: center;
}

.item img {
   width: 100%;
   height: 150px;
   
}

.buttons {
    display: flex;
    justify-content: center;
   
}

.buttons button {
   margin-left: 5px;
   background-color: rgb(0,159,128);
   color: #fff;
   font-weight: bold;
   margin-bottom: 20px;
}

.buttons .remove {
    background-color: rgb(207, 28, 28);

}
.buttons h4 {
    margin-left: 4px;
   
}

.check {
    text-align: center;
    margin-bottom: 50px;
}

.check button {
    margin-left: 10px;
}

/*cart end*/



@media only screen and (max-width: 600px) {
    .mainProducts {
       
        width: 50%;

       
    
    }

    .cart {
       
        width: 50%;
       
        
    }

  
    }
   

    .shop-img img {
        width: 150px;
        position: absolute;
        top: 80px;
        left: 0px;
        
        
        
        
    }

  
   
    
   

   
  

  @media only screen and (max-width: 319px) {

    .mainProducts {
       
        width: 65%;
        margin-left: 40px;
        
    
    }

   

    .cart {
       
        width: 65%;
       
        
    }

    .shop-img h1 {
        font-size: 50px;
    }

    


    .shop-img img {
        width: 150px;
        position: absolute;
        top: 80px;
        left: 0px;
        
        
        
        
    }

  

   


    
  }


  @media (min-width:320px) and (max-width:321px) {

    .mainProducts {
       
        width: 65%;
        margin-left: 60px;
        
    
    }

    .cart {
        margin-left: 65px;
    }


  }


  @media (min-width:540px) and (max-width:541px) {

    
.mainProducts {
 
    width: 40%;
  

}



.cart {
    width: 40%;
}



  }

  
 